import React, {Component} from 'react';
import {Button, Form, Input, Table, Tag} from "antd";
import {Link, withRouter} from "react-router-dom";
import {connect} from "react-redux";
import * as moment from "moment";
import SelectScap from "../../../../components/Select/SelectScap";
import actions from "../../../../redux/report/actions";
import ExcelExport from './excel'
import {currencyFormat} from "../../../../utils/helper/utilities";



@withRouter
@connect(({ transaction, report }) => ({
  transaction,
  report,
  contentStatus: transaction.contentStatus,
  customerList : transaction.customerList,
  reportSalesCustomer : report.reportSalesCustomer
}))
class ReportCustomer extends Component {
  constructor(props) {
    super(props);

    this.componentRef = React.createRef();
    this.state = {
      modalReturn: false,
      from: moment().startOf('month').format("YYYY-MM-DD"),
      to: moment().format("YYYY-MM-DD"),
      custCode:""
    }
  }

  handleSearch = () => {
    const { dispatch} = this.props;

    const { from, to, custCode} = this.state

    const payload = {
      from,
      to,
      custCode,
    }

    dispatch({
      type:actions.REPORT_SALES_CUSTOMER,
      payload
    })

  }

  componentWillUnmount() {
    const { dispatch} = this.props;


    dispatch({
      type:actions.SET_STATE,
      payload: {
        reportSalesCustomer: []
      }
    })
  }


  render() {

    const { from, to, custCode} = this.state
    const {customerList, reportSalesCustomer, report: {loadingReportCustomer}} = this.props

    const dt = (customerList || [])
      .map(el => ({
        title: el.custName || "",
        value: el.custCode || ""
    }))

    const datas = [...dt, {title: 'ALL', value: 'all'}]

    const columns = [
      {
        "title": "Kode",
        "dataIndex" : "CustCode",
        key: "CustCode",
        width: 100,
      },
      {
        "title": "Nama Anggota",
        "dataIndex" : "CustName",
        key: "CustName",
        width: 200,
      },
      {
        "title": "No. Transaksi",
        "dataIndex" : "SiNo",
        key: "SiNo",
        width: 200,
      },
      {
        "title": "Tanggal",
        "dataIndex" : "SiDate",
        key: "SiDate",
        render: d => moment(d).format("DD-MM-YYYY"),
        width: 150,
      },

      {
        "title": "Status",
        "dataIndex" : "VoidBill",
        key: "VoidBill",
        width: 100,
        render : t => (
          <div>
            {t === 1 && <Tag color='red'>BATAL</Tag> }
            {t === 0 && <Tag color='green'>SUKSES</Tag>}
          </div>
        )
      },
      {
        "title": "Pembayaran",
        "dataIndex" : "TenderName",
        key: "TenderName",
        width: 100,
      },

      {
        "title": "Nama Produk",
        "dataIndex" : "ItemName",
        key: "ItemName",
        width: 250,
      },

      {
        "title": "Qty",
        "dataIndex" : "QtySi",
        key: "QtySi",
        align: 'right',
        width: 100,
        render: d => currencyFormat(d, 0)
      },
      {
        "title": "Harga",
        "dataIndex" : "UnitPrice",
        key: "UnitPrice",
        align: 'right',
        width: 150,
        render: d => currencyFormat(d, 0)
      },
      {
        "title": "Sub Total",
        "dataIndex" : "Amount",
        key: "Amount",
        align: 'right',
        width: 150,
        render: d => currencyFormat(d, 0)
      },
      {
        "title": "Diskon",
        "dataIndex" : "DiscVal",
        key: "DiscVal",
        align: 'right',
        width: 150,
        render: d => currencyFormat(d, 0)
      },
      {
        "title": "Total",
        "dataIndex" : "Amount",
        key: "Amount",
        align: 'right',
        width: 150,
        render: d => currencyFormat(d, 0)
      },
    ]



    return (
      <div className='container mx-auto p-4 h-screen'>
        <div>
          <Link to ='/kasir'>
            <Button className='bg-green-500 text-white rounded leading-3'>ORDER BARU</Button>
          </Link>

          <Link to ='/pos/report'>
            <Button className='bg-green-500 text-white rounded leading-3 ml-4'>LAPORAN</Button>
          </Link>
        </div>


        <div className='mt-4'>
          <Form layout='inline'>
            <Form.Item label='Tanggal '>
              <Input value={from} type='date' onChange={e => this.setState({from: e.target.value})} />
            </Form.Item>
            <Form.Item>
              <Input value={to} type='date' onChange={e => this.setState({to: e.target.value})} />
            </Form.Item>
            <Form.Item label='Anggota'>
              <SelectScap style={{width: "200px"}} value={custCode} datas={datas} onChange={e => this.setState({custCode: e})} />
            </Form.Item>
            <Form.Item>
              <Button type='primary' onClick={this.handleSearch}>Cari</Button>
            </Form.Item>
            <Form.Item>
              <ExcelExport
                filename='Laporan_Penjualan_By_Anggota'
                type='primary'
                date={`${from}_${to}`}
                dataitem={ reportSalesCustomer}
              />
            </Form.Item>

          </Form>


        </div>

        <div className='mt-4'>
          <Table
            dataSource={reportSalesCustomer}
            columns={columns}
            size='small'
            loading={loadingReportCustomer}
            scroll={{y: 400}}

          />

        </div>


      </div>
    );
  }
}

export default ReportCustomer;

