import XLSX from 'tempa-xlsx'

const strToArrBuffer = s => {
  const buf = new ArrayBuffer(s.length)
  const view = new Uint8Array(buf)

  for (let i = 0; i !== s.length; i += 1) {
    // eslint-disable-next-line no-bitwise
    view[i] = s.charCodeAt(i) & 0xff
  }

  return buf
}

const dateToNumber = (v, date1904) => {
  if (date1904) {
    v += 1462
  }

  const epoch = Date.parse(v)

  return (epoch - new Date(Date.UTC(1899, 11, 30))) / (24 * 60 * 60 * 1000)
}

const excelSheetFromDataSet = dataSet => {
  /*
    Assuming the structure of dataset
    {
        xSteps?: number; //How many cells to skips from left
        ySteps?: number; //How many rows to skips from last data
        columns: [array | string]
        data: [array_of_array | string|boolean|number | CellObject]
        fill, font, numFmt, alignment, and border
    }
     */
  if (dataSet === undefined || dataSet.length === 0) {
    return {}
  }

  const ws = {}
  const range = { s: { c: 10000000, r: 10000000 }, e: { c: 0, r: 0 } }
  let rowCount = 0

  dataSet.forEach(dataSetItem => {
    const { columns, data, xSteps: xS, ySteps: yS } = dataSetItem

    const xSteps = typeof xS === 'number' ? xS : 0
    const ySteps = typeof yS === 'number' ? yS : 0
    if (dataSet === undefined || dataSet.length === 0) {
      return
    }

    rowCount += ySteps

    const columnsWidth = []
    if (columns.length >= 0) {
      columns.forEach((col, index) => {
        const cellRef = XLSX.utils.encode_cell({ c: xSteps + index, r: rowCount })
        fixRange(range, 0, 0, rowCount, xSteps, ySteps)
        // let colTitle = col;
        if (typeof col === 'object') {
          // colTitle = col.title;
          columnsWidth.push(
            col.width || { wpx: 80 },
          ) /* wch (chars), wpx (pixels) - e.g. [{wch:6},{wpx:50}] */
        }
        getHeaderCell(/* colTitle, */ col, cellRef, ws)
      })

      rowCount += 1
    }

    if (columnsWidth.length > 0) {
      ws['!cols'] = columnsWidth
    }

    // eslint-disable-next-line no-plusplus
    for (let R = 0; R !== data.length; ++R, rowCount++) {
      // eslint-disable-next-line no-plusplus
      for (let C = 0; C !== data[R].length; ++C) {
        const cellRef = XLSX.utils.encode_cell({ c: C + xSteps, r: rowCount })
        fixRange(range, R, C, rowCount, xSteps, ySteps)
        getCell(data[R][C], cellRef, ws)
      }
    }
  })

  if (range.s.c < 10000000) {
    ws['!ref'] = XLSX.utils.encode_range(range)
  }

  return ws
}

function getHeaderCell(v, cellRef, ws) {
  const cell = {}
  const fontBold = { font: { bold: true } }
  const headerCellStyle = { ...v.style, ...fontBold }
  cell.v = v.title
  cell.t = 's'
  cell.s = headerCellStyle
  ws[cellRef] = cell
}

function getCell(v, cellRef, ws) {
  // assume v is indeed the value. for other cases (object, date...) it will be overriden.
  const cell = { v }
  if (v === null) {
    return
  }

  const isDate = v instanceof Date
  if (!isDate && typeof v === 'object') {
    cell.s = v.style
    cell.v = v.value
    v = v.value
  }

  if (typeof v === 'number') {
    cell.t = 'n'
  } else if (typeof v === 'boolean') {
    cell.t = 'b'
  } else if (isDate) {
    cell.t = 'n'
    // eslint-disable-next-line prefer-destructuring
    cell.z = XLSX.SSF.table[14]
    cell.v = dateToNumber(cell.v)
  } else {
    cell.t = 's'
  }
  ws[cellRef] = cell
}

function fixRange(range, R, C, rowCount, xSteps /* ySteps */) {
  if (range.s.r > R + rowCount) {
    range.s.r = R + rowCount
  }

  if (range.s.c > C + xSteps) {
    range.s.c = C + xSteps
  }

  if (range.e.r < R + rowCount) {
    range.e.r = R + rowCount
  }

  if (range.e.c < C + xSteps) {
    range.e.c = C + xSteps
  }
}

const excelSheetFromAoA = data => {
  const ws = {}
  const range = { s: { c: 10000000, r: 10000000 }, e: { c: 0, r: 0 } }

  // eslint-disable-next-line no-plusplus
  for (let R = 0; R !== data.length; ++R) {
    // eslint-disable-next-line no-plusplus
    for (let C = 0; C !== data[R].length; ++C) {
      if (range.s.r > R) {
        range.s.r = R
      }

      if (range.s.c > C) {
        range.s.c = C
      }

      if (range.e.r < R) {
        range.e.r = R
      }

      if (range.e.c < C) {
        range.e.c = C
      }

      const cell = { v: data[R][C] }
      if (cell.v === null) {
        // eslint-disable-next-line no-continue
        continue
      }

      const cellRef = XLSX.utils.encode_cell({ c: C, r: R })
      if (typeof cell.v === 'number') {
        cell.t = 'n'
      } else if (typeof cell.v === 'boolean') {
        cell.t = 'b'
      } else if (cell.v instanceof Date) {
        cell.t = 'n'
        // eslint-disable-next-line prefer-destructuring
        cell.z = XLSX.SSF.table[14]
        cell.v = dateToNumber(cell.v)
      } else {
        cell.t = 's'
      }

      ws[cellRef] = cell
    }
  }

  if (range.s.c < 10000000) {
    ws['!ref'] = XLSX.utils.encode_range(range)
  }

  return ws
}

export { strToArrBuffer, dateToNumber, excelSheetFromAoA, excelSheetFromDataSet }
