import React from 'react'
import { Button } from 'antd'
import * as moment from 'moment'
import ReactExport from '../../../../components/ExcelBuilder'


const { ExcelFile } = ReactExport
const { ExcelSheet /* ExcelColumn */ } = ExcelFile

const alignRight = { alignment: { horizontal: 'right' } }
const numFmt = '#,##0.00;(#,##0.00)'

const columns = [
  { title: 'No_Transaksi', width: { wch: 25 } }, // char width
  { title: 'Tanggal', width: { wch: 15 } }, // char width
  { title: 'Status', width: { wch: 15 } }, // char width
  { title: 'Kode_Anggota', width: { wch: 15 } }, // char width
  { title: 'Nama_Anggota', width: { wch: 30 } }, // char width
  { title: 'Nama_Produk', width: { wch: 30 } }, // char width
  { title: 'Qty', width: { wch: 10 }, style: alignRight }, // char width
  { title: 'Harga', width: { wch: 15 }, style: alignRight }, // char width
  { title: 'Total', width: { wch: 15 }, style: alignRight }, // char width
  { title: 'Pembayaran', width: { wch: 15 }, style: alignRight }, // char width
  { title: 'Bayar_Kartu', width: { wch: 15 }, style: alignRight }, // char width
  { title: 'Bayar_Cash', width: { wch: 15 }, style: alignRight }, // char width
  { title: 'Kembalian_Cash', width: { wch: 15 }, style: alignRight }, // char width
  { title: 'Remark_Batal', width: { wch: 30 } }, // char width

]

const columnsHeader = [{ title: '', width: { wch: 10 } }]

class ExcelAllJournal extends React.Component {
  render() {
    const { dataitem=[], filename, date } = this.props
    const numFormat = { numFmt, ...alignRight }
    // console.log(dataitem, 'DATA ITEMMMMD')

    const dataHeader = [
      [{ value: filename, style: { font: { bold: true } } }],
      [{ value: `DATE ${date}`, style: { font: { bold: true } } }],
    ]


    // const formatCell = value => (typeof value === 'number' ? value : value.replace(/[,]/g, ''))

    const data = (dataitem || []).map(el => [
      { value: el.SiNo },
      { value: moment(el.SiDate).format('DD MMM YYYY') },
      { value: el.VoidBill === 1 ? "BATAL" :"SUKSES" },
      { value: el.CustCode  ||"" },
      { value: el.CustName || "" },
      { value: el.ItemName || "" },
      {
        value: +el.QtySi || 0,
        style: {
          ...numFormat,
        },
      },
      {
        value: +el.UnitPrice || 0,
        style: {
          ...numFormat,
        },
      },
      {
        value: +el.Amount|| 0,
        style: {
          ...numFormat,
        },
      },
      { value: el.TenderName || "" },
      {
        value: +el.CreditCard || 0,
        style: {
          ...numFormat,
        },
      },
      {
        value: +el.CashPaid || 0,
        style: {
          ...numFormat,
        },
      },
      {
        value: +el.CashChange || 0,
        style: {
          ...numFormat,
        },
      },
      { value: el.SiRemark || "" },
    ])

    /**
     * Columns : Array of Object
     * data: Array Multidemensional
     * */
    const multiDataSet = [
      { columns: columnsHeader, data: dataHeader },
      { columns, data, ySteps: 3 },
    ]

    return (
      <div>
        <ExcelFile
          filename={`${filename}_${date}`}
          element={<Button {...this.props}>Excel</Button>}
        >
          <ExcelSheet dataSet={multiDataSet} name={filename} />
        </ExcelFile>
      </div>
    )
  }
}

export default ExcelAllJournal
